   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
html,
body {
  height: 100%;
  font-size: 12px;
  line-height: 1;
  font-family: arial, "Hiragino Sans GB";
  background: #f5f7f9;
  overflow: hidden;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
a {
  text-decoration: none;
}
input[type="text"]::-ms-clear {
  display: none;
}
html {
  font-size: 100px;
}
body {
  padding-top: constant(safe-area-inset-top);
  padding-left: constant(safe-area-inset-left);
  padding-right: constant(safe-area-inset-right);
  padding-bottom: constant(safe-area-inset-bottom);
}
#app .page {
  width: 3.75rem;
  height: 6.04rem;
  -webkit-transform-origin: top left;
          transform-origin: top left;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}
.hide {
  display: none !important;
}
.show {
  display: block !important;
}
.hidden {
  visibility: hidden !important;
}
.visibility {
  visibility: visible !important;
}
.home {
  background: url(/static/img/home.29f6bcf.png) no-repeat;
  background-size: cover;
}
.home .home-text {
  position: absolute;
  top: 0.94rem;
  height: 2.27rem;
  left: 0.62rem;
  right: 0.52rem;
  background: url(/static/img/home-text.7687a56.png) no-repeat;
  background-size: cover;
}
.home .word-in-an {
  -webkit-animation: word-in 4s linear 1;
          animation: word-in 4s linear 1;
}
.home .notice-button {
  display: none;
  position: absolute;
  bottom: 1.39rem;
  left: 1rem;
  right: 1rem;
  height: 1.15rem;
  -webkit-animation: tada 1s linear infinite;
  animation-fill-mode: forwards;
  -webkit-animation-fill-mode: forwards;
  animation-delay: .5s;
  -webkit-animation-delay: .5s;
  background: url(data:image/png;base64,) no-repeat;
  background-size: cover;
}
@-webkit-keyframes word-in {
0% {
    height: 0;
    opacity: 0;
}
20% {
    height: 0.5rem;
    opacity: 1;
}
40% {
    height: 1rem;
    opacity: 1;
}
60% {
    height: 1.5rem;
    opacity: 1;
}
80% {
    height: 2rem;
    opacity: 1;
}
100% {
    height: 2.27rem;
    opacity: 1;
}
}
@keyframes word-in {
0% {
    height: 0;
    opacity: 0;
}
20% {
    height: 0.5rem;
    opacity: 1;
}
40% {
    height: 1rem;
    opacity: 1;
}
60% {
    height: 1.5rem;
    opacity: 1;
}
80% {
    height: 2rem;
    opacity: 1;
}
100% {
    height: 2.27rem;
    opacity: 1;
}
}
@-webkit-keyframes tada {
0% {
    -webkit-transform: scale(0.9);
}
10%,
  20% {
    -webkit-transform: scale(1);
}
30%,
  50%,
  70%,
  90% {
    -webkit-transform: scale(1.1);
}
40%,
  60%,
  80% {
    -webkit-transform: scale(1.05);
}
100% {
    -webkit-transform: scale(0.95);
}
}

